import React, { PureComponent } from "react";
import ReactApexChart from "react-apexcharts";
import ApexCharts from "apexcharts";
export default class SentimentChart extends PureComponent {
  constructor(props) {
    super(props);

    const getData = this.props.contentAnalyticsData.newSeries[0]['data']

    const lastCategory = getData && Object.keys(getData).length && this.props.contentAnalyticsData.newSeries[0]['data'].length - 1; // Use dynamic category if needed
    const seriesData   = getData && Object.keys(getData).length && this.props.contentAnalyticsData.newSeries[0]['data'];

    console.log(seriesData[lastCategory],'sdfsdfsdfsdfsdfsdf')

    const maxYvalue = 9000;
    const mingraph = (25*maxYvalue)/100
    const mixgraph = (75*maxYvalue)/100

    this.state = {
      series: this.props.contentAnalyticsData.newSeries,
      options: {
        colors: ["#546FED"],
        chart: {
          id: "area-datetime",
          toolbar: { show: false },
          type: "line",
          animations: {
            enabled: true,
            easing: "linear",
            dynamicAnimation: {
              speed: 1000,
            },
          },
          height: 350,
          zoom: { enabled: false },
        },
        legend: {
          show: true,
          position: "top",
          itemMargin: {
            horizontal: 20,
            vertical: 0,
          },
        },
        dataLabels: { enabled: false },
        markers: { 
        size: 4, style: "hollow" },
        stroke: {
          width: [4],
          curve: "straight",
        },
        xaxis: {
          type: "datetime",
          min: this.props.contentAnalyticsData?.unixDate[0],
          tickAmount: 6,
        },
        annotations: {
          points: [
            {
              x:  lastCategory && seriesData[lastCategory][0], // Dynamically set based on last category
              y:  lastCategory && seriesData[lastCategory][1], // Last data point
              marker: {
                size: 8, // Highlight the last marker
                fillColor: "#546FED",
                strokeColor: "#2545d7",
                shape: "circle",
                cssClass: "blinking-marker", // Apply custom CSS class for blinking effect
              },
              label: {
                borderColor: "#546FED",
                offsetY: -10, // Move the label higher to avoid overlap
                offsetX: -10, // Move the label higher to avoid overlap
                style: {
                  color: "#546FED",
                  background: "transparent", // Make label background transparent
                  zIndex: 0, // Lower z-index to ensure marker is on top
                },
                text: `${lastCategory && seriesData[lastCategory][1]}`,
              },
            }],
          yaxis: [
            {
              y: mingraph,
              y2: 0,
              borderColor: "transparent",
              fillColor: "#f7caca", // Color for 0-30 range
            },
            {
              y: mixgraph,
              y2: mingraph,
              borderColor: "transparent",
              fillColor: "#f9debe", // Color for 30-60 range
            },
            {
              y: maxYvalue,
              y2: mixgraph,
              borderColor: "transparent",
              fillColor: "#cfe7d5", // Color for 60-100 range
            },
          ],
        },
        tooltip: { x: { format: "dd MMM yyyy" } },
        grid: {
          show: false, // Enable or disable the entire grid (optional)
        },
      },
      responsive: [
        {
          // breakpoint: 991,
          options: {
            chart: {
              height: 300,
            },
          },
          // breakpoint: 767,
          options: {
            chart: {
              height: 280,
            },
          },
        },
      ],
    };
  }

  render() {
    const { contentAnalyticsData } = this.props;

    const lastDate =
      contentAnalyticsData?.unixDate[
        contentAnalyticsData?.unixDate?.length - 1
      ];
    if (lastDate) {
      const chart = ApexCharts.getChartByID("area-datetime2");
      if (chart) {
        chart.zoomX(
          contentAnalyticsData?.unixDate[0],
          contentAnalyticsData?.unixDate[lastDate - 1]
        );
      }
    }


    return (
     <> 
      <ReactApexChart
        options={this.state.options}
        series={this.state.series}
        type="line"
        height={350}
      />
           
     </> 
    );
  }
}
